.headerContainer {
  display: flex;
  align-items: center;
  min-width: 380px;
  height: 62px;
  background: url(../../assets/nav-background.png);

  .headerLogo {
    width: 115px;
    height: 32px;
    margin-left: 24px;
    background-image: url(../../assets/logo.png);
    background-image: image-set(url(../../assets/logo.png) 1x, url(../../assets/logo@2x.png) 2x );
    background-repeat: no-repeat;
    background-size: 115px 32px; 
  }
}